import {Component} from '@angular/core';


@Component({
  selector: 'cnyt-mint',
  templateUrl: './cnyt-mint.component.html',
  styleUrls: ['./cnyt-mint.component.css']
})
export class CnytMintComponent {
  toggle1 = false;
  toggle2 = false;
  toggle3 = false;
  toggle4 = false;
  gotoggle1(): void {
    this.toggle1 = !this.toggle1;
  }
  gotoggle2(): void {
    this.toggle2 = !this.toggle2;
  }
  gotoggle3(): void {
    this.toggle3 = !this.toggle3;
  }
  gotoggle4(): void {
    this.toggle4 = !this.toggle4;
  }
}



