import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {CnytComponent} from './cnyt.component';
import {HkdtComponent} from './hkdt.component';
import {CnytMintComponent} from './cnyt-mint.component';
import {CnytRedeemptionComponent} from './cnyt-redeemption.component';
import {RedeemComponent} from './redeem.component';
import {RedeemBankComponent} from './redeem-bank.component';
import {RedeemTokenComponent} from './redeem-token.component';
import {RedeemRequestComponent} from './redeem-request.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/cnyt/cnytmint',
    pathMatch: 'full'
  },
  {
    path: 'cnyt',
    component: CnytComponent,
    children: [
      {
        path: 'cnytmint',
        component: CnytMintComponent
      },
      {
        path: 'cnytredeemption',
        component: CnytRedeemptionComponent
      }
    ]
  },
  {
    path: 'hkdt',
    component: HkdtComponent
  },
  {
    path: 'redeem',
    component: RedeemComponent,
  },
  {
    path: 'redeemtoken',
    component: RedeemTokenComponent
  },
  {
    path: 'redeemrequest',
    component: RedeemRequestComponent
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})

export class AppRoutingModule {}



